
import { ListenerManager } from "../../../../frame/scripts/Manager/ListenerManager";
import { HitTest } from "../../../../frame/scripts/Utils/HitTest";
import { UIHelp } from "../../../../frame/scripts/Utils/UIHelp";
import { EventType } from "../../Data/EventType";
import { EditorManager, OptionData } from "../../Manager/EditorManager";
import DetailNode from "./DetailNode";
import TeacherItem from "./TeacherItem";
import TempItem from "./TempItem";

const { ccclass, property } = cc._decorator;

@ccclass
export default class TeacherConfig extends cc.Component {

    @property(cc.ToggleContainer)
    private toggle_panTi: cc.ToggleContainer = null;
    @property(cc.ToggleContainer)
    private toggle_tiaoTi: cc.ToggleContainer = null;

    @property(cc.EditBox)
    private countEditBox1: cc.EditBox = null;
    @property(cc.EditBox)
    private countEditBox2: cc.EditBox = null;

    @property(cc.Node)
    private itemNode: cc.Node = null;
    @property(cc.Node)
    private layoutNode: cc.Node = null;


    private _gameData = null;
    public updateOptionPanel() {
        if (EditorManager.editorData.GameData == null) {
            EditorManager.editorData.GameData = JSON.parse(JSON.stringify(new OptionData()));
        }
        console.log("EditorManager.editorData.GameData", EditorManager.editorData.GameData);

        this._gameData = EditorManager.editorData.GameData;

        this.countEditBox1.string = this._gameData.countValue1;
        this.countEditBox2.string = this._gameData.countValue2;
        this.setLayout();

    }


    private editingChanged_count1() {
        let index = this.countEditBox1.string.indexOf("\n")
        if (index != -1) {
            this.countEditBox1.string = this.countEditBox1.string.replace('\n', '');
            this.countEditBox1.blur();
        }
    }

    private editingDidEnded_count1() {
        if (this.countEditBox1.string.length == 0) {
            this.countEditBox1.string = "";
        } else {
            let value = Number(this.countEditBox1.string);
            if (value > 4) {
                this.countEditBox1.string = "4";
            } else if (value <= 0) {
                this.countEditBox1.string = "1";
            }
        }

        this._gameData.countValue1 = this.countEditBox1.string;
        this.updateOptionPanel();
    }

    private editingChanged_count2() {
        let index = this.countEditBox2.string.indexOf("\n")
        if (index != -1) {
            this.countEditBox2.string = this.countEditBox2.string.replace('\n', '');
            this.countEditBox2.blur();
        }
    }

    private editingDidEnded_count2() {
        if (this.countEditBox2.string.length == 0) {
            this.countEditBox2.string = "";
        } else {
            let value = Number(this.countEditBox2.string);
            if (value > 4) {
                this.countEditBox2.string = "1";
            } else if (value <= 0) {
                this.countEditBox2.string = "0";
            }
        }

        this._gameData.countValue2 = this.countEditBox2.string;
        this.updateOptionPanel();
    }


    private tempData = {
        kapaiId: "", wenben: "", guanlianId: ""
    }

    private setLayout() {
        this.layoutNode.removeAllChildren();
        if (this._gameData.countValue1 == "" || this._gameData.countValue2 == "") {
            this._gameData.titleData = [];
            return;
        }

        let value = Number(this._gameData.countValue1) * Number(this._gameData.countValue2);

        if (value % 2 != 0) {
            this._gameData.countValue1 = "";
            this._gameData.countValue2 = "";
            this._gameData.titleData = [];
            this.updateOptionPanel();
            return;
        }

        if (this._gameData.titleData.length == 0) {
            for (let i = 0; i < value; i++) {
                this._gameData.titleData.push(JSON.parse(JSON.stringify(this.tempData)));
            }
        } else {
            if (value > this._gameData.titleData.length) {
                let tempValue = value - this._gameData.titleData.length;
                for (let i = 0; i < tempValue; i++) {
                    this._gameData.titleData.push(JSON.parse(JSON.stringify(this.tempData)));
                }
            } else if (value < this._gameData.titleData.length) {
                let tempArr = [];
                for (let index = 0; index < value; index++) {
                    tempArr.push(this._gameData.titleData[index]);
                }
                this._gameData.titleData = tempArr;
            }
        }

        let arr = [0, 200, 450, 700, 950];
        this.layoutNode.width = arr[Number(this._gameData.countValue1)];
        for (let i = 0; i < this._gameData.titleData.length; i++) {
            let item = cc.instantiate(this.itemNode)
            item.parent = this.layoutNode;
            item.active = true;
            item.name = i.toString();
            item.getComponent(TeacherItem).initItem(i, this._gameData.titleData);
        }
    }


    // 卡牌随机
    private kaPaiSuiJi() {
        if (this._gameData.countValue1 == "" || this._gameData.countValue2 == "") {
            this._gameData.titleData = [];
            UIHelp.showTip("先输入格子数")
            return;
        }

        let value = Number(this._gameData.countValue1) * Number(this._gameData.countValue2);
        let value2 = value / 2;
        let arr = new Array(0, 1, 2, 3, 4, 5, 6, 7);
        let tempData = this.getData();
        let tempData2 = [];
        for (let i = 0; i < value2; i++) {
            let randomIndex = Math.floor(Math.random() * arr.length);
            let data = JSON.parse(JSON.stringify(tempData[arr[randomIndex]]));
            for (let j = 0; j < data.length; j++) {
                tempData2.push(data[j])
            }
            arr.splice(randomIndex, 1);
        }

        this._gameData.titleData = this.shuffle(tempData2);
        this.updateOptionPanel();
    }

    private getData() {
        let data = [
            [{ kapaiId: "101", wenben: "1kg", guanlianId: "1" }, { kapaiId: "102", wenben: "1000g", guanlianId: "1" }],
            [{ kapaiId: "201", wenben: "1.8米", guanlianId: "2" }, { kapaiId: "202", wenben: "180cm", guanlianId: "2" }],
            [{ kapaiId: "301", wenben: "1km", guanlianId: "3" }, { kapaiId: "302", wenben: "1000m", guanlianId: "3" }],
            [{ kapaiId: "401", wenben: "1cm", guanlianId: "4" }, { kapaiId: "402", wenben: "10mm", guanlianId: "4" }],
            [{ kapaiId: "501", wenben: "500克", guanlianId: "5" }, { kapaiId: "502", wenben: "0.5千克", guanlianId: "5" }],
            [{ kapaiId: "601", wenben: "0.3m", guanlianId: "6" }, { kapaiId: "602", wenben: "30厘米", guanlianId: "6" }],
            [{ kapaiId: "701", wenben: "1t", guanlianId: "7" }, { kapaiId: "702", wenben: "1吨", guanlianId: "7" }],
            [{ kapaiId: "801", wenben: "1克", guanlianId: "8" }, { kapaiId: "802", wenben: "1000mg", guanlianId: "8" }]
        ]
        return data;
    }

    //复制数组
    private copyArray(arr) {
        return JSON.parse(JSON.stringify(arr))
    }


    // 打乱数组
    private shuffle(array) {
        let newArr = this.copyArray(array)
        for (let i = newArr.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [newArr[i], newArr[j]] = [newArr[j], newArr[i]];
        }
        return newArr;
    }



    // 自动判题
    public onTogglePanTi(toggle: cc.Toggle): void {
        let index = this.toggle_panTi.toggleItems.indexOf(toggle);
        EditorManager.editorData.isPanTi = 0 === index;
    }

    // 自动跳题
    public onToggleTiao(toggle: cc.Toggle): void {
        let index = this.toggle_tiaoTi.toggleItems.indexOf(toggle);
        EditorManager.editorData.isTiaoTi = 0 === index;
    }


    private getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1) + min);
    }











}
